@use 'base/mixins' as m;
@use 'base/variables' as v;

.confirmar,.restablecer{
    
    @include m.entrada(v.$verde);

    svg{
        width: 7rem;
        display: block;
        margin: 0 auto;

        &.confirmado{
            color: v.$verde;
        }

        &.no-confirmado{
            color: v.$rojo;
        }
    }

    p{
        text-align: center;
        font-size: 3rem;
    }
}