@use 'base/mixins' as m;
@use 'base/variables' as v;



.modal{
    position: absolute;
    z-index: 1;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    display: block;
    margin: 0 auto;
    
    background: rgba(29, 28, 28, 0.541);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 4px );
    -webkit-backdrop-filter: blur( 4px );

    display: grid;
    place-content: center;

    .formulario{
        
       
        legend{
            display: block;
            text-align: center;
            font-size: 2rem;
            font-weight: 700;
            color: v.$blanco;
        }

        input[type="text"]{
            border: none;
            margin: 2rem 0;
            padding: 1rem;
            border-radius: .5rem
        }

        label{
            font-size: 2rem;
            font-weight: 700;
            color: v.$blanco;
            flex-basis: 5rem
        }

        .cerrar-modal{
            @include m.boton(v.$rosa, v.$blanco);
            margin-top: 2rem;
        }


        @include m.tablet{ 
            width: 50rem;
        }



        //animacion
        position: relative;
        top: 50%;
        transform: translateY(-50%);

        animation-name: descender;
        animation-duration: .5s;
        animation-timing-function: ease-in-out;


        &.cerrar{  
            animation-name: cerrar;
            animation-duration: .5s;
            animation-timing-function: ease-in-out;
        }
    }
}

@keyframes descender {
    
    0%{
        top: -40rem;
        opacity: 0;
    }

    75%{
        top: 80%;
    }

    100%{
        top: 50%;
        opacity: 1;
    }
}


@keyframes cerrar {
    

    100%{
        top: -40rem;
        opacity: 0;
    }
}

.swal2-html-container{
    font-size: 1.6rem !important; 
}