@use 'base/variables' as v;
@use 'base/mixins' as m;


.barra{
    background-color: v.$blanco;
    padding: 0 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .user,a{

        display: flex;
        align-items: center;
        gap: 1rem;
    }

    svg{
        width: 3rem;
        
        color: v.$indigo;
    }

    a{
        text-decoration: none;
        color: v.$negro;
        padding: .5rem;
        border-radius: .5rem;
        transition: all .3s;

        &:hover{
            background-color: v.$indigo;
            color: v.$blanco;

            svg{
                color: v.$blanco;
            }
        }
    }

    -webkit-box-shadow: -1px 25px 10px -23px rgba(0,0,0,0.39);
    -moz-box-shadow: -1px 25px 10px -23px rgba(0,0,0,0.39);
    box-shadow: -1px 25px 10px -23px rgba(0,0,0,0.39);


}


.barra-mobile{

    background-color: v.$indigo;
    padding: 2rem 3rem;
    display: flex;
    justify-content: space-between;
    align-items:center;
    gap: 2rem;
    @include m.tablet{ 
        display: none;
    }
    h1{
        color: v.$blanco;
        margin: 0;
    }

    #menu-mobile{
        width: 5rem;
        filter: invert(100);
        cursor: pointer;
    }

    @include m.tablet{ 
        display: none;
    }
}