@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    
    background-color: v.$grisClaro;

    display: flex;
    flex-direction: column;
    height: 100vh;

    @include m.tablet{ 
        flex-direction: row;
       // height: 100%;
    }

    .principal{
        width: 100%;
        flex-grow: 1;
    }

    
    .contenido{
        padding: 1rem;

        .nombre-pagina{
            text-align: center;
            margin-top: 2rem;
            font-size: 3rem;
        }
    }


    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;
        }

        input[type="submit"]{
            @include m.boton(v.$indigo,v.$blanco);
        }
    }


    .nombre-pagina{
        font-style: italic;
        position: relative;

    }

}

.cambiar-password{
    display: flex;
    margin-top: 1rem;
    .enlace-password{
        @include m.boton(v.$cyan, v.$blanco);
        display: inline-block;
        padding: 1rem;
    
        margin: 0 auto;
    }
}
