@use 'base/mixins' as m;
@use 'base/variables' as v;


.acciones{
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 2rem;

    a{
        color: v.$negro;
        &:hover{
            color: darken(v.$gris, 10);
            font-weight: 700;
        }
    }

    @include m.tablet{ 
        flex-direction: row;
        justify-content: space-evenly;
    }
}