@use 'base/mixins' as m;
@use 'base/variables' as v;



.principal{
    position: relative;

    @include m.tablet{ 
        overflow-y: auto;
    }
}

.proyectos-container{
    padding: 2rem 4rem;
    display: grid;
    grid-template-columns: repeat( auto-fit, minmax(250px, 1fr) );
    gap: 3rem;
   
    .proyecto{

        border-radius: 1rem;
        min-height: 10rem;
        display: flex;
        align-items: center;
        justify-content: center;

        -webkit-box-shadow: 6px 12px 21px -12px rgba(0,0,0,1);
        -moz-box-shadow: 6px 12px 21px -12px rgba(0,0,0,1);
        box-shadow: 6px 12px 21px -12px rgba(0,0,0,1);
        background-color: v.$indigo;

        border-radius: 2rem;
        transition: all .3s;

        &:hover{
            transform: scale(1.1);
            background-color: v.$blanco;

            .proyecto-nombre{
                color: v.$indigo;
            }
        }
    }

    .proyecto-nombre{
        font-size:2rem;
        font-weight: 900;
        text-align: center;
        margin: 0;
        padding:2rem;
        color: v.$blanco;
        text-transform: capitalize;
        font-family: v.$fuente_headings;
    }


    .vacio{
        p{
            text-align: center;
            display: block;
            font-size: 3rem;
        }
    
        a{
            text-align: center;
            @include m.boton(v.$indigo, v.$blanco);
            max-width: 30rem;

        }
    }
    
}