html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #f3f4f6;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
}

.tagline {
  text-align: center;
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
}

/** Media Queries **/
.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex-basis: 10rem;
}
.formulario .campo input {
  flex-grow: 1;
  border: none;
  background-color: #d8dadf;
  padding: 1rem;
  border-radius: 1rem;
}
.formulario .campo input:focus {
  background-color: #bdc0c7;
}

.acciones {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
.acciones a {
  color: #000;
}
.acciones a:hover {
  color: #545964;
  font-weight: 700;
}
@media (min-width: 768px) {
  .acciones {
    flex-direction: row;
    justify-content: space-evenly;
  }
}

.alerta {
  text-align: center;
  padding: 0.5rem;
  display: block;
  width: 80%;
  margin: 0 auto;
  margin-top: 0.5rem;
  text-transform: uppercase;
  color: #FFFFFF;
  position: relative;
  height: 3rem;
}
.alerta::before {
  content: "";
  display: block;
  position: absolute;
  width: 1rem;
  height: 3rem;
  top: 0;
  left: 0;
}
.alerta.error {
  background-color: #ff5d5d;
}
.alerta.error::before {
  background-color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #00ab00;
}
.alerta.exito::before {
  background-color: rgb(0, 120, 0);
}

.correcto {
  background-color: rgb(0, 120, 0);
}

button.swal2-styled.swal2-confirm {
  background-color: #2563EB;
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .acciones a {
  color: #0891B2;
}

