.sidebar {
  background-color: #4338CA;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.5s;
  height: 0;
}
.sidebar h2 {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 2rem;
}
.sidebar .sidebar-nav {
  padding-bottom: 3rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 1.5rem;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
  transition: all 300ms;
}
.sidebar .sidebar-nav a:hover {
  background-color: #28217b;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav {
    margin: 5rem 0;
  }
}
.sidebar .sidebar-nav .activo {
  background-color: #28217b;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
  }
}
.sidebar.mostrar {
  opacity: 1;
  height: 20rem;
  padding: 0;
  position: absolute;
  top: 10rem;
  z-index: 1;
  width: 100%;
  -webkit-box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 7px 5px 0px rgba(0, 0, 0, 0.75);
}
.sidebar.mostrar h2 {
  display: none;
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    opacity: 1;
    height: 100%;
    position: relative;
    top: 0;
    width: 50%;
  }
  .sidebar.mostrar h2 {
    display: block;
  }
}

.principal {
  position: relative;
}
@media (min-width: 768px) {
  .principal {
    overflow-y: auto;
  }
}

.proyectos-container {
  padding: 2rem 4rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 3rem;
}
.proyectos-container .proyecto {
  border-radius: 1rem;
  min-height: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-box-shadow: 6px 12px 21px -12px rgb(0, 0, 0);
  -moz-box-shadow: 6px 12px 21px -12px rgb(0, 0, 0);
  box-shadow: 6px 12px 21px -12px rgb(0, 0, 0);
  background-color: #4338CA;
  border-radius: 2rem;
  transition: all 0.3s;
}
.proyectos-container .proyecto:hover {
  transform: scale(1.1);
  background-color: #FFFFFF;
}
.proyectos-container .proyecto:hover .proyecto-nombre {
  color: #4338CA;
}
.proyectos-container .proyecto-nombre {
  font-size: 2rem;
  font-weight: 900;
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #FFFFFF;
  text-transform: capitalize;
  font-family: "Inter", sans-serif;
}
.proyectos-container .vacio p {
  text-align: center;
  display: block;
  font-size: 3rem;
}
.proyectos-container .vacio a {
  text-align: center;
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
  max-width: 30rem;
}
@media (min-width: 768px) {
  .proyectos-container .vacio a {
    width: auto;
  }
}
.proyectos-container .vacio a:hover {
  background-color: #352ca3;
  cursor: pointer;
}

