.modal {
  position: absolute;
  z-index: 1;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  margin: 0 auto;
  background: rgba(29, 28, 28, 0.541);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  display: grid;
  place-content: center;
}
.modal .formulario {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  animation-name: descender;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}
.modal .formulario legend {
  display: block;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  color: #FFFFFF;
}
.modal .formulario input[type=text] {
  border: none;
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 0.5rem;
}
.modal .formulario label {
  font-size: 2rem;
  font-weight: 700;
  color: #FFFFFF;
  flex-basis: 5rem;
}
.modal .formulario .cerrar-modal {
  background-color: #DB2777;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0 auto;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #b11e5f;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal .formulario {
    width: 50rem;
  }
}
.modal .formulario.cerrar {
  animation-name: cerrar;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}

@keyframes descender {
  0% {
    top: -40rem;
    opacity: 0;
  }
  75% {
    top: 80%;
  }
  100% {
    top: 50%;
    opacity: 1;
  }
}
@keyframes cerrar {
  100% {
    top: -40rem;
    opacity: 0;
  }
}
