@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-nueva-tarea{


    display: flex;
    flex-direction: column;
    gap: 2rem;

    @include m.tablet{ 
        flex-direction: row;
        justify-content: space-around;
    }

    .agregar-tarea{
        @include m.boton(v.$cyan , v.$blanco);
        padding: 1.5rem 2rem;
    }
    .borrar-proyecto{
        @include m.boton(v.$rojo , v.$blanco);

        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1.5rem 2rem;

        svg{
            width: 2rem;
        }
    }
}


.tareasContenedor{
    padding: 0 3rem;
    margin-bottom: 5rem;
    background-color: v.$grisClaro;
    h3{
        text-align: center;
        font-size: 2rem;
        margin-top: 5rem;
    }

    .mensaje{
        text-align: center;
    }
}

.tarea{
    position: relative;

    p{
        margin-bottom: 1rem;
        margin-top: 0;
        @include m.tablet{ 
            margin: 0;
        }
    }

    &::before{
        content: '';
        background-color: v.$indigo;
        height: 1rem;
        width: 1rem;
        border-radius: 50%;
        display: block;
        position: absolute;

        left: 0;
        top: 38px;
        
    }

    .boton-borrar{
        @include m.boton(v.$rojo , v.$blanco);
    }

    .boton-estado{
        &.completa{
            @include m.boton(v.$morado , v.$blanco);
        }

        &.pendiente{
            @include m.boton(v.$naranja , v.$blanco);
        }
    }

    button{
        padding: 0.5rem !important;
        border-radius: 1rem;
        width: 50rem;
        text-transform: uppercase;
    }


    .opciones{
        display: flex;
        gap: 1rem;
        justify-content: space-between;
    }

    @include m.tablet{ 
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    border-bottom: 1px solid v.$indigo;
    padding: 2.5rem 0;
    padding-left: 2rem;

    &:last-child{
        border-bottom: none;
    }
}

.filtros{
    margin-top: 3rem;
    padding:0 4rem;

    .filtros-inputs{
        background-color: v.$gris;
        border-radius: 1rem;
        padding: 2rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        justify-content: space-around;
    
        .campo{
            display: flex;
            gap: 2rem;
            align-items: center;

            width: 100%;
            justify-content: center;
            label{
                flex-basis: 10rem;
            }

            @include m.tablet{ 
                gap: 1rem;

                label{
                    flex-basis: unset;
                }
            }
        }
    
        input[type="radio"]{
            width: 1.6rem;
            height: 1.6rem;
            accent-color: v.$indigo;
        }

        @include m.tablet{ 
            flex-direction: row;
        }
    }
}
