@use 'base/variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 0 auto;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin  entrada($color){

    margin-top: 5rem;
    .uptask{
        background: linear-gradient(to right, $color 0%, lighten($color, 30));
        color: transparent;
        background-clip: text;
        text-align: center;

        font-size: 7rem;

        @include tablet{ 
            font-size: 10rem;
        }
    }

    input[type="submit"]{
        @include boton($color,v.$blanco);
    }

    .acciones{
        a{
            color: $color;
        }
    }
}