@use 'base/variables' as v;


.alerta{
    text-align: center;
    padding: .5rem;
    display: block;
    width: 80%;
    margin: 0 auto;
    margin-top: .5rem;
    text-transform: uppercase;
    color: v.$blanco;
    position: relative;
    height: 3rem;

    &::before{
        content: '';
        display: block;
        position: absolute;
        width: 1rem;
        height: 3rem;
        top: 0;
        left: 0;
    }

    &.error{
        background-color: lighten(v.$rojo, 35);

        &::before{
            background-color: v.$rojo;
        }
     }
    
     &.exito{
        background-color: lighten(v.$verde, 10);

        &::before{
            background-color: v.$verde;
        }
     }
    

}



.correcto{
    background-color: v.$verde;
}


button.swal2-styled.swal2-confirm{
    background-color: v.$azul;
}