@use 'base/mixins' as m;
@use 'base/variables' as v;


.mensaje{
    
    @include m.entrada(v.$naranja);

    svg{
        color: v.$verde;
        width: 7rem;
        display: block;
        margin: 0 auto;
    }

    p{
        text-align: center;
        font-size: 3rem;
    }
}