@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{

    margin-top: 3rem;

    .campo{
        display: flex;
        align-items: center;
        gap: 1rem;
        margin-bottom: 2rem;

        label{
            flex-basis: 10rem;
        }

        input{
            flex-grow: 1;
            border: none;
            background-color: lighten(v.$gris, 40);
            padding: 1rem;
            border-radius: 1rem;
            &:focus{
                background-color: lighten(v.$gris, 30);
            }
        }
    }
}