@use 'base/mixins' as m;
@use 'base/variables' as v;

.sidebar{

    background-color: v.$indigo;

    h2{
        text-align: center;
        color: v.$blanco;
        margin: 0;
        padding: 2rem;
    }


    .sidebar-nav{

        padding-bottom: 3rem;

        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 1.5rem;
            padding: 1rem;
            text-align: center;
            text-transform: uppercase;
            transition: all 300ms;
            &:hover{
                background-color: darken(v.$indigo, 20%);
            }
        }


        @include m.tablet{ 
            margin: 5rem 0;
        }


        .activo{
            background-color: darken(v.$indigo, 20%);
        }
    }


    opacity: 0;
    transition-property: opacity;
    transition-duration: .5s;
    height: 0;

    @include m.tablet{ 
        opacity: 1;
        height: 100%;
    }


    &.mostrar{

        opacity: 1;
        height: 20rem;
        padding: 0;
        
        h2{
            display: none;
        }

        position: absolute;
        top: 10rem;
        z-index: 1;
        width: 100%;


        -webkit-box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.75);
        box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.75);

        @include m.tablet{ 
            opacity: 1;
            height: 100%;
            position: relative;
            top: 0;
            
            width: 50%;
            h2{
                display: block;
            }
        }
    }
}





